<template>
  <div id="good-pre-page">
    <header-nav title="" />
    <van-swipe :autoplay="3000">
      <van-swipe-item v-for="(image, index) in images" :key="index" class="good-pic-box">
        <img v-lazy="image" class="good-pic" />
      </van-swipe-item>
    </van-swipe>
    <div class="good-info-box">
      <div class="good-info">
        <div class="name">{{ name }}</div>
      </div>
      <p class="good-desc">{{ desc }}</p>
      <van-progress :percentage="progress" color="#f44" class="good-progress" />
      <div class="treasure-info-panel">
        <div class="treasure-target treasure-info">
          <span>{{ target }}</span>
          <span></span>
        </div>
        <div class="treasure-countdown treasure-info">
          <span>{{ countdown }}</span>
          <span></span>
        </div>
        <div class="treasure-people treasure-info">
          <span>{{ support }}</span>
          <span></span>
        </div>
      </div>
    </div>
    <van-cell title="" class="treasure-history" @click="onClickHistory" is-link />
    <van-steps direction="vertical" >
      <van-step>
        <h3>xxxx </h3>
        <p>2016-07-12 12:40</p>
      </van-step>
      <van-step>
        <h3>xxxx </h3>
        <p>2016-07-11 10:00</p>
      </van-step>
      <van-step>
        <h3>xxxx </h3>
        <p>2016-07-10 09:30</p>
      </van-step>
    </van-steps>
    <van-goods-action class="btn-bottom">
      <van-goods-action-big-btn @click="handleBetClick" text="" primary />
    </van-goods-action>
    <van-actionsheet v-model="showPanel" title="" class="bet-panel">
      <div class="bet-form">
        <van-cell title="" :value="`${unitPrice}`" />
        <van-cell title="">
          <van-stepper v-model="betCount" />
        </van-cell>
        <van-cell title="" :value="`${totalPrice}`" />
        <van-button size="large" type="danger" @click="submit" class="submit-btn"></van-button>
      </div>
    </van-actionsheet>
  </div>
</template>

<script>
import {
  Swipe,
  SwipeItem,
  Tag,
  GoodsAction,
  GoodsActionBigBtn,
  Progress,
  Cell,
  Steps,
  Step,
  Actionsheet,
  Stepper,
  Button,
  Toast
} from 'vant'
import HeaderNav from '@/components/HeaderNav'

export default {
  data() {
    return {
      id: 0,
      name: '',
      desc: '',
      progress: 30,
      target: '10000',
      countdown: 29,
      support: 146,
      images: ['static/good1.jpg', 'static/good2.jpg', 'static/good3.jpg', 'static/good4.jpg'],
      showPanel: false,
      unitPrice: 3,
      betCount: 1
    }
  },
  computed: {
    totalPrice() {
      return this.unitPrice * this.betCount
    }
  },
  components: {
    HeaderNav,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [Tag.name]: Tag,
    [GoodsAction.name]: GoodsAction,
    [GoodsActionBigBtn.name]: GoodsActionBigBtn,
    [Progress.name]: Progress,
    [Cell.name]: Cell,
    [Steps.name]: Steps,
    [Step.name]: Step,
    [Actionsheet.name]: Actionsheet,
    [Stepper.name]: Stepper,
    [Button.name]: Button,
    [Toast.name]: Toast
  },
  methods: {
    onClickHistory() {
      this.$router.push(`/winner-history/${this.id}`)
    },

    handleBetClick() {
      this.showPanel = true
    },

    submit() {
      this.State.isLogin ? Toast.success('') : this.$router.push('/login')
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$bus.$emit('hide-footer')
      vm.id = vm.$route.params.id
    })
  },
  beforeRouteLeave(to, from, next) {
    const footerActiveIndex = 2
    this.$bus.$emit('show-footer', footerActiveIndex)
    next()
  }
}
</script>

<style scoped>
#good-pre-page {
  margin-bottom: 50px;
}
.good-pic-box {
  background: #fff;
}
.good-pic {
  width: 100%;
  height: 240px;
}
.good-info-box {
  margin-bottom: 10px;
  padding: 15px;
  background: #fff;
  box-shadow: 3px 3px 3px #ccc;
  font-size: 16px;
}
.good-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 600;
}
.good-desc {
  margin: 10px 0 20px;
  font-size: 14px;
}
.good-progress {
  margin-bottom: 20px;
}
.treasure-info-panel {
  display: flex;
  padding: 10px;
  background: #e1e1e1;
}
.treasure-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 1 1 auto;
}
.treasure-countdown {
  border-left: 1px solid #ccc;
  border-right: 1px solid #ccc;
}
.treasure-history {
  margin-bottom: 10px;
  box-shadow: 3px 3px 3px #ccc;
}
.btn-bottom {
  z-index: 10;
}
.bet-panel {
  font-size: 16px;
}
.bet-form {
  padding: 20px;
}
</style>



